.wrapper {
  max-width: 940px;
  width: 100%;
  margin: 0 auto;
}

.wrapper-content {
  display: flex;
}

.header {
  overflow: hidden;
}

.line {
  width: 660px;
  height: 13px;
  background: url("../img/line.png");
  display: block;
  margin: 50px 0;
}

#preloader {
  position: fixed;
  background: #e8603c;
  width: 100%;
  height: 400%;
  z-index: 5;
}

#circularG {
  position: absolute;
  width: 68px;
  height: 68px;
  margin: auto;
  top: calc(12.5% - 34px);
  left: calc(50% - 34px);
}

.circularG {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.98);
  width: 16px;
  height: 16px;
  border-radius: 10px;
  animation-name: bounce_circularG;
  animation-duration: 1.1s;
  animation-iteration-count: infinite;
  animation-direction: normal;
}

#circularG_1 {
  left: 0;
  top: 27px;
  animation-delay: -0.41s;
}

#circularG_2 {
  left: 7px;
  top: 7px;
  animation-delay: -0.55s;
}

#circularG_3 {
  top: 0;
  left: 27px;
  animation-delay: -0.69s;
}

#circularG_4 {
  right: 7px;
  top: 7px;
  animation-delay: -0.83s;
}

#circularG_5 {
  right: 0;
  top: 27px;
  animation-delay: -0.97s;
}

#circularG_6 {
  right: 7px;
  bottom: 7px;
  animation-delay: -1.1s;
}

#circularG_7 {
  left: 27px;
  bottom: 0;
  animation-delay: -1.24s;
}

#circularG_8 {
  left: 7px;
  bottom: 7px;
  animation-delay: -1.38s;
}

@keyframes bounce_circularG {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.3);
  }
}

.logo-nav-wrapper {
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-family: 'BebasNeueRegular', sans-serif;
  position: relative;
}

.logo {
  font-size: 44px;
  position: relative;
}

.logo__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #e8603c;
  height: 100%;
  font-weight: bold;
  letter-spacing: 2px;
  margin-left: 1px;
}

.logo__link span {
  color: #f3af9d;
}

.menu__item {
  display: inline-block;
  margin: 17px 0 0 25px;
  letter-spacing: 1px;
}

.menu__link {
  color: #b9b9b9;
  text-decoration: none;
  font-size: 19px;
  font-weight: bold;
}

.menu__link:hover {
  color: #e8603c;
}

.menu__link_active {
  color: #e8603c;
}

.blog {
  height: 61px;
  background: #e8603c;
  margin-top: 16px;
  position: relative;
  margin-bottom: 24px;
}

.blog:before, .blog:after {
  position: absolute;
  content: "";
  height: 61px;
  background: #f2ae9c;
  width: 1000px;
  top: 0;
}

.blog:before {
  right: 100%;
  margin-right: 10px;
}

.blog:after {
  left: 100%;
  margin-left: 10px;
}

.blog__title {
  text-transform: uppercase;
  color: #fff;
  font-family: 'BebasNeueRegular', sans-serif;
  font-size: 30px;
  padding: 15px 0 0 18px;
  letter-spacing: 1px;
}

.main {
  width: 670px;
}

.block {
  width: 670px;
  position: relative;
}

.block-img {
  height: 300px;
}

.block-ico {
  position: absolute;
  width: 75px;
  height: 75px;
  background-color: #e8603c;
  left: -85px;
  background-position: center center;
  background-repeat: no-repeat;
}

.block-content {
  display: flex;
  margin-top: 20px;
}

.block-info {
  width: 100%;
  margin: 2px 0 0 1px;
}

.block-info__title {
  color: #e8603c;
  text-transform: uppercase;
  font-family: 'BebasNeueRegular', sans-serif;
  font-size: 16px;
}

.block-info__text {
  color: #bababa;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-size: 12px;
  margin: 7px 0 13px 0;
}

.block-text {
  margin-left: 35px;
}

.block-text__title {
  color: #e8603c;
  text-transform: uppercase;
  font-family: 'BebasNeueRegular', sans-serif;
  font-size: 21px;
  letter-spacing: 1px;
}

.block-text__text {
  color: #414141;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-size: 12px;
  margin-top: 17px;
  line-height: 17px;
}

.block-text_alone {
  margin-left: 0;
}

.blockquote {
  padding: 30px 50px 0 80px;
  width: 654px;
  height: 75px;
  background: url("../img/bg-quote.jpg") #f5f5f5;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-style: italic;
  font-size: 12px;
  color: #474747;
  position: relative;
}

.blockquote:before {
  position: absolute;
  content: "";
  width: 25px;
  height: 20px;
  background-image: url("../img/quote1.png");
  top: 25px;
  left: 36px;
  background-position: center center;
  background-repeat: no-repeat;
}

.link {
  color: #bababa;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-size: 12px;
  margin: 7px 0 13px 0;
  text-decoration: none;
}

.buttons {
  display: flex;
}

.buttons__link {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  font-family: 'BebasNeueRegular', sans-serif;
  margin-right: 5px;
}

.buttons__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
  background-color: #f3af9d;
}

.buttons__item_active {
  background: #e8603c;
}

.aside {
  width: 220px;
  margin-left: 52px;
}

.small-block {
  width: 100%;
  margin-bottom: 30px;
}

.small-block__text {
  color: #414141;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  font-size: 12px;
  margin-top: 17px;
  line-height: 15px;
}

.small-block__link {
  display: block;
  text-decoration: none;
  color: #414141;
  font-family: 'HelveticaNeueCyr-Roman', sans-serif;
  line-height: 15px;
}

.title-bg {
  display: flex;
}

.title-bg__title {
  color: #e8603c;
  text-transform: uppercase;
  font-family: 'BebasNeueRegular', sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  white-space: nowrap;
  margin: 2px 15px 0 0;
  letter-spacing: 1.3px;
}

.title-bg__bg {
  width: 100%;
  height: 13px;
  background: url("../img/line.png");
  display: block;
  margin-top: 4px;
}

.gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 5px;
  margin-top: 18px;
}

.gallery__img {
  background-color: #f3af9d;
  width: 50px;
  height: 50px;
  filter: grayscale(100%);
  transition: 0.3s;
}

.gallery__img:hover {
  filter: grayscale(0);
  transform: scale(0.97);
}
